import React, { memo } from 'react';
import { Handle, Position } from '@xyflow/react';


const NodeType1 =  memo(({ data, isConnectable=true }) => {

  if(!data.noDisplayData.noHandles){

    return (
      <div className="custom-node" key={data.id}>
        <Handle type="target" position={Position.Top} id="top" isConnectable={isConnectable}/>
        <Handle type="target" position={Position.Bottom} id="bottom" isConnectable={isConnectable} />
        <Handle type="target" position={Position.Left} id="left" isConnectable={isConnectable}/>
        <Handle type="target" position={Position.Right} id="right" isConnectable={isConnectable}/>

        <div>
        {/* <div style={data.noDisplayData.pushDownText ? {background: 'none'}: {}}> */}
            {data.noDisplayData.pushDownText ? <div style={{height: 50, width: 50, backgroundColor: data.noDisplayData.nodeType === "Bifurcation"?'red':'green', borderRadius: '30px 30px 30px 30px ', margin: 'auto'}}></div>: <div></div>}   
            {Object.entries(data).map(([key, value]) => 
                key !== "noDisplayData" && key !== "id" && value !="" && 
                (key !== "" ? 
                  key !== "label" ? 
                  <div key = {String(key) + String(data["id"])} style={{display: 'flex'}}>          
                      <label style={{whiteSpace: 'nowrap'}}><strong>{key}</strong></label> &nbsp;
                      <label className="display-linebreak">{value}</label>
                  </div> 
                  :
                  <div key = {String(key) + String(data["id"])} style={{paddingTop:  ["Bifurcation", "Junction"].includes(data.noDisplayData.nodeType) ? 0: 0}}>             
                      <label className="display-linebreak" style={{fontSize: 25}}><strong>{value}</strong></label>    
                  </div>
                : 
                <label key = {String(key) + String(data["id"])}                                                                             // notes field
                  style={{ whiteSpace: 'pre-line' }}
                  dangerouslySetInnerHTML={{ __html: value.replace(/\n/g, '<br>') }}
                />)
            )}
        </div>
        
        <Handle type="source" position={Position.Top} id="top" isConnectable={isConnectable} />
        <Handle type="source" position={Position.Bottom} id="bottom" isConnectable={isConnectable}/>
        <Handle type="source" position={Position.Left} id="left" isConnectable={isConnectable}/>
        <Handle type="source" position={Position.Right} id="right" isConnectable={isConnectable}/>
      </div>
    )
  }else{
    return (                    // To display Legends in the flow 
      <div className="custom-node" key={data.id}>
        <div>
        {/* <div style={data.noDisplayData.pushDownText ? {background: 'none'}: {}}> */}
            {data.noDisplayData.pushDownText ? <div style={{height: 50, width: 50, backgroundColor: data.noDisplayData.nodeType === "Bifurcation"?'red':'green', borderRadius: '30px 30px 30px 30px ', margin: 'auto'}}></div>: <div></div>}   
            {Object.entries(data).map(([key, value]) => 
                key !== "noDisplayData" && key !== "id" && value !="" && 
                (key !== "" ? 
                  key !== "label" ? 
                  <div key = {String(key) + String(data["id"])} style={{display: 'flex'}}>          
                      <label style={{whiteSpace: 'nowrap'}}><strong>{key}</strong></label> &nbsp;
                      <label className="display-linebreak">{value}</label>
                  </div> 
                  :
                  <div key = {String(key) + String(data["id"])} style={{paddingTop:  ["Bifurcation", "Junction"].includes(data.noDisplayData.nodeType) ? 0: 0}}>             
                      <label className="display-linebreak" style={{fontSize: 25}}><strong>{value}</strong></label>    
                  </div>
                : 
                <label key = {String(key) + String(data["id"])}                                                                               // notes field
                  style={{ whiteSpace: 'pre-line' }}
                  dangerouslySetInnerHTML={{ __html: value.replace(/\n/g, '<br>') }}
                />)
            )}
        </div>
      </div>
    )
  }
});


const BifurcationNode =  memo(({ data, isConnectable }) => {
  return (
    <div className="custom-node">
      <Handle type="target" position={Position.Top} id="top" />
      <Handle type="target" position={Position.Bottom} id="bottom" />
      <Handle type="target" position={Position.Left} id="left" />
      <Handle type="target" position={Position.Right} id="right" />

      <div>
          {Object.entries(data).map(([key, value]) => 
              key !== "id" && value !="" && 
              (key !== "" ? 
                key !== "label" ? 
                <div key = {String(key) + String(data["id"])}>             
                    <label><strong>{key}</strong></label> &nbsp;
                    <label className="display-linebreak">{value}</label>    
                </div> 
                :
                <div key = {String(key) + String(data["id"])}>             
                    <label className="display-linebreak" style={{fontSize: 25}}><strong>{value}</strong></label>    
                </div>
              : 
              <label                                                                              // notes field
                style={{ whiteSpace: 'pre-line' }}
                dangerouslySetInnerHTML={{ __html: value.replace(/\n/g, '<br>') }}
              />)
          )}
      </div>
      
      <Handle type="source" position={Position.Top} id="top" />
      <Handle type="source" position={Position.Bottom} id="bottom" />
      <Handle type="source" position={Position.Left} id="left" />
      <Handle type="source" position={Position.Right} id="right" />
    </div>
  );
});

export {NodeType1}