// Choose File button
function JsonFileInput({setRawData}) {
  const [fileContent, setFileContent] = useState("");

  function handleFileSelect(event) {
    const file = event.target.files[0];
    if(!file){return}
    const reader = new FileReader();
    reader.onload = () => {
        setRawData(reader.result);
    };
    reader.readAsText(file);
  }
  return (
    <Panel position={'top-center'} style={{margin: 0, padding: 0}}>
        <div style={{width: '100vw', height: 50, backgroundColor: 'green', display: 'flex', alignItems: 'center', justifyContent: 'center', color: 'white' }}>
            <input type="file" onChange={handleFileSelect} />
            {fileContent && <p>File content: {fileContent}</p>}
        </div>
    </Panel>
  );
}

// tree layout
const alignNodes = (nodeObjArray, edgeObjArray) => {
    console.log(nodeObjArray, edgeObjArray)
    // degre implementation
    nodeObjArray.forEach(item => g.setNode(item.id,    { label: item.id,  width: 100, height: 500 }))
    edgeObjArray.forEach(item => g.setEdge(item.source, item.target))
    dagre.layout(g);

    nodeObjArray = nodeObjArray.map(item => {
        item.position = {x:0, y: 0}
        item.position.y = g.node(item.id)["x"]
        item.position.x = g.node(item.id)["y"]
        console.log(item.id, g.node(item.id)["x"], g.node(item.id)["y"])
        return item
    })

    console.log(nodeObjArray)
    return nodeObjArray
}

const getSnappingSides = (nodes, edge) => {
    const sourceNode = nodes.find((node) => node.id === edge.source);
    const targetNode = nodes.find((node) => node.id === edge.target);

    const calculateAngle = (source, target) => {
        const deltaX = target.position.x - source.position.x;
        const deltaY = target.position.y - source.position.y;
        const angle = Math.atan2(deltaY, deltaX) * (180 / Math.PI);
        return angle;
      };
    
    var sourceHandle 
    var targetHandle

    if(edge.sourceHandleLocked && edge.targetHandleLocked){
        sourceHandle = edge.sourceHandle
        targetHandle = edge.targetHandle
        return {sourceHandle, targetHandle}
    }

    var angle = calculateAngle(sourceNode, targetNode)
    if (angle > -45 && angle <= 45) {
        sourceHandle = 'right'; 
        targetHandle = 'left' 
    } else if (angle > 45 && angle <= 135) {
        sourceHandle = 'bottom'; 
        targetHandle = 'top' 
    }else if(angle > -135 && angle <= -45) {
        sourceHandle= 'top';
        targetHandle= 'bottom';
    } else {
        sourceHandle= 'left';
        targetHandle= 'right' 
    }

    // if locked keep the original handle despite calculated value
    if(edge.sourceHandleLocked){sourceHandle = edge.sourceHandle}
    if(edge.targetHandleLocked){targetHandle = edge.targetHandle}

    return {sourceHandle, targetHandle}
}

