import React, { useEffect, useState } from "react";
import CryptoJS from "crypto-js";


const AuthPage = ({ setAuthenticated }) => {
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  const [accessExpired, setAccessExpired] = useState(false)

  const storedHashedPassword = "ed49887b003c3fc77e98e329dd814453eeecee252775e8f088d27008d81c71e8";

  // Handle login
  const handleLogin = () => {
    const hashedInputPassword = CryptoJS.SHA256(password).toString();
    if (hashedInputPassword === storedHashedPassword) {
      setAuthenticated(true); // Successful login
    } else {
      setError("Invalid password");
    }
  };

  useEffect(() => {
    const isPastEndOf2024 = () => new Date() > new Date('2024-12-31T23:59:59');
    setAccessExpired(isPastEndOf2024)
  }, [])

  return (
    !accessExpired 
    ? 
    <div style={{display: 'flex', flexDirection: 'column', width: 300, alignItems:'center', margin:'auto'}}>
      <h2>Login</h2>
      <input
        type="password"
        value={password}
        placeholder="Enter password"
        onChange={(e) => setPassword(e.target.value)}
        style={{padding: 6, textAlign:'center'}}
      />
      &nbsp;<button onClick={handleLogin}>Login</button>

      {error && <p style={{ color: "red" }}>{error}</p>}
    </div> 
    : 
    <p style={{ color: "red" }}>{"Access Expired. [Rebuild Required] "}</p>
  );
};

export default AuthPage;
