import React, {memo} from 'react';
import { getSmoothStepPath, getStraightPath } from '@xyflow/react';
import { getBezierPath } from '@xyflow/react';

const edgeStyles = {
  normal: { stroke: '#555', strokeWidth: 2, arrowHeadType: 'arrowclosed', arrowHeadColor: '#555' },
  highlighted: { stroke: '#f00', strokeWidth: 3, arrowHeadType: 'arrowclosed', arrowHeadColor: '#f00' },
};

const CustomEdgeSmooth = memo(
  ({
    id,
    sourceX,
    sourceY,
    targetX,
    targetY,
    sourcePosition,
    targetPosition,
    style = {
      strokeWidth: 3, // Width of the visible edge
      stroke: '#009ce0', // Color of the edge
      strokeDasharray: '5', // Dashed line style
    },
    markerEnd,
    onEdgeClick, // onClick handler passed as a prop
  }) => {
    const [edgePath, labelX, labelY] = getBezierPath({
      sourceX,
      sourceY,
      sourcePosition,
      targetX,
      targetY,
      targetPosition,
    });

    return (
      <>
        <path
          id={id}
          style={style}
          className="react-flow__edge-path"
          d={edgePath}
          markerEnd={markerEnd}
        />

        {/* Transparent clickable path */}
        <path
          id={`${id}-clickable`}
          className="react-flow__edge-path-clickable"
          d={edgePath}
          style={{
            fill: 'none',
            stroke: 'transparent',
            strokeWidth: 100, // Larger click target
            cursor: 'pointer',
          }}
          // onClick={(event) => onEdgeClick(event, id)} // Handle click events here
        />
      </>
    );
  }
);


const CustomEdgeStepped = memo(({ id, sourceX, sourceY, 
                      targetX, targetY, sourcePosition, 
                      targetPosition, style = {
                        strokeWidth: 3,      // Width of the edge
                        stroke: '#009ce0',      // Color of the edge
                        strokeDasharray: '5', // Dashed line style
                      }, markerEnd }) => {
  const [edgePath, labelX, labelY] = getSmoothStepPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
  });

  return (
    <>
      <path id={id} 
          style={style} 
          className="react-flow__edge-path" 
          d={edgePath} 
          markerEnd={markerEnd} />


      <path
        id={`${id}-clickable`}
        className="react-flow__edge-path-clickable"
        d={edgePath}
        style={{
          fill: 'none',
          stroke: 'transparent',
          strokeWidth: 100, // This makes the click target larger without changing the visible edge size
          cursor: 'pointer',
        }}
      />
    </>
  );
});

const CustomEdgeStraight = memo(({ id, sourceX, sourceY, 
  targetX, targetY, sourcePosition, 
  targetPosition, style = {
    strokeWidth: 3,      // Width of the edge
    stroke: '#009ce0',      // Color of the edge
    strokeDasharray: '5', // Dashed line style
  }, markerEnd }) => {
const [edgePath, labelX, labelY] = getStraightPath({
sourceX,
sourceY,
sourcePosition,
targetX,
targetY,
targetPosition,
});

return (
<>
<path id={id} 
style={style} 
className="react-flow__edge-path" 
d={edgePath} 
markerEnd={markerEnd} />


<path
id={`${id}-clickable`}
className="react-flow__edge-path-clickable"
d={edgePath}
style={{
fill: 'none',
stroke: 'transparent',
strokeWidth: 100, // This makes the click target larger without changing the visible edge size
cursor: 'pointer',
}}
/>
</>
);
});

export {CustomEdgeSmooth, CustomEdgeStepped, CustomEdgeStraight};